import { combineReducers } from 'redux';

//Child reducers
import { tasks } from './TaskList';
import { newTaskMessage } from './NewTask';
import { searchValue } from './SearchTask';
import { allTasksCompleted } from './AllTasksCompleted';

const scheduler = combineReducers({
    tasks,
    newTaskMessage,
    searchValue,
    allTasksCompleted,
});

export default scheduler;
